
//menu selectors
const menuLink = document.querySelector(".menu-wrap");
const menuClose = document.querySelector(".full-screen-menu-close");


const toggleMenu = () => {
	const fullScreenMenu = document.querySelector(".full-screen-menu");
	fullScreenMenu.classList.toggle("full-screen-menu-active");
	document.querySelector("section").classList.toggle("blurry-background");
}


menuLink.addEventListener("click", toggleMenu);
menuClose.addEventListener("click", toggleMenu);




