import './navbar.js';

const countDownDate = new Date("March 28, 2019 00:00:00").getTime();

window.setInterval(() => { 
  const now = new Date().getTime();
  const distance = countDownDate - now;
  const days = Math.floor(distance / (1000 * 60 * 60 * 24));
  const hours = Math.floor((distance % (1000 * 60 * 60 * 24)) / (1000 * 60 * 60));
  const minutes = Math.floor((distance % (1000 * 60 * 60)) / (1000 * 60));
  const seconds = Math.floor((distance % (1000 * 60)) / 1000);

  document.getElementById("timerResult").innerHTML = `
    <div class="timeUnit"><h2>${days}</h2><p>DAYS</p></div>
    <div class="timeUnit"><h2>${hours}</h2><p>HOURS</p></div>
    <div class="timeUnit"><h2>${minutes}</h2><p>MINUTES</p></div>
    <div class="timeUnit"><h2>${seconds}</h2><p>SECONDS</p></div>
  `
}, 1000);

